<template>
  <main class="home">
    <h1>hasty</h1>
    <h3>hello Developer</h3>
    <a @click="changeRoute">跳转</a>
    <ShowHome />
  </main>
</template>

<script>
import ShowHome from '@/components/showHome.vue';
import { mapState } from 'vuex';
import Dao from '@/services';
import ppo from '@/utils/ppo';
export default {
  name: 'Home',
  components: {
    ShowHome
  },
  data() {
    return {
      loading: false,
      homeData: {}
    };
  },
  computed: mapState({
    list: (state) => state.list
  }),
  mounted() {
    this.getIndexData();
  },
  methods: {
    async getIndexData() {
      let res = await Dao.get('GetIndexData', {
        loginType: 'qq',
        tpid: 319238
      });
      this.indexData = res;
    },
    changeRoute() {
      this.$router.push({
        name: 'projects',
        query: {
          a: 1,
          uuid: ppo.uuid()
        }
      });
    },
  }
};
</script>
